.show_cons_2 {
  color: #fff;
  background-color: #000000;
  padding: 2rem;
  border-top: 2px solid #0c0b0b;
}

.Info_component {
  display: flex;
  gap: 4rem;
  align-items: flex-start;
}

.col_box_1 h2 {
  font-size: 29px;
  font-family: "Rubik";
  line-height: 4rem;
  border-bottom: 2px solid #fff;
}

.evaluate p {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
}

.evaluate span {
  font-size: 19px;
  font-weight: 400;
  margin-left: 1rem;
}

.para {
  font-size: 15px;
  line-height: 1.5;
  font-weight: 300;
  color: #ddd;
  font-family: "Open Sans";
}

.clip,
.col_box_2 {
  width: 100%;
  border-radius: 7px;
}

/* Detail má»¥c TopRating */

.box-detail {
  margin: 0 auto;
  width: 89%;
  color: #fff;
  background-color: #000000;
  padding: 2rem;
  border-top: 2px solid #0c0b0b;
}

.boxAlbum img {
  width: 100%;
  height: 18rem;
  border-radius: 2px;
}
