.Table_of_contents {
  height: 50vh;
  background-size: cover;
  font-family: "Open Sans", sans-serif;
}

.inner-space {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: -0.3rem;
  width: 100%;
  padding-left: 0.9%;
  position: fixed;
  z-index: 2;
}

.blackScroll {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #f00;
  background-color: #212529;
  width: 100%;
  padding-left: 0.9%;
  position: fixed;
  z-index: 2;
}

.Box_Icon {
  display: inline-block;
}

.Box_Icon .svg-inline--fa {
  width: 6%;
  margin-left: 73%;
  margin-top: 0.7rem;
  display: inline-block;
}

.Box_Icon .svg-inline--fa:hover {
  cursor: pointer;
  transform: scale(0.9);
}

.inner-space h3 {
  font-size: 22px;
  color: red;
}

.inner-space h3:hover {
  cursor: pointer;
}

.blackScroll h3 {
  font-size: 21px;
}

.blackScroll h3:hover {
  cursor: pointer;
}
