.Card-search {
  height: 170vh;
}

.fa-solid {
  color: #000000;
  font-size: 21px;
}

.form-control-search {
  margin-top: -16rem;
}

.card-form {
  margin: 0 auto;
  width: 41%;
  background-color: #fff;
}

.form-item-1 input {
  width: 89%;
  padding: 1rem;
  background-color: #fff;
  outline: none;
  border: none;
  font-size: 18px;
}

.form-item-1 {
  display: flex;
  align-items: center;
  border-bottom: 3px solid #4dabf7;
  margin-top: 2rem;
}

.btn-form button {
  padding: 0.7rem;
  width: 14%;
  font-size: 16px;
  margin-left: 4%;
  border: none;
  margin-bottom: 2rem;
  background-color: #fff;
  border: none;
  border-radius: 4px;
}

.btn-form button:hover {
  transform: translateY(-2px);
  cursor: pointer;
}

.btn-form {
  display: flex;
  grid-gap: 4%;
  gap: 4%;
  justify-content: flex-end;
  margin-top: 2rem;
  position: relative;
  left: -3%;
}

.blueBtn {
  background-color: #339af0 !important;
  color: #fff;
}

.list-movie-search img {
  width: 100%;
  height: 13rem;
}

.list-movie-search img:hover {
  transform: scale(0.9);
  transition: transform 0.2s ease-in;
}

.card-movie-search h2 {
  font-size: 24px;
  margin-bottom: 3rem;
  color: #fff;
}

.card-movie-search {
  margin: 0 auto;
  width: 90%;
}

.card-movie-search {
  margin-top: 9rem;
  margin-bottom: 3rem;
}

.list-movie-search {
  display: grid;
  grid-template-columns: repeat(9, 10%);
  grid-template-rows: 13rem;
  grid-column-gap: 1.1rem;
  grid-row-gap: 1.1rem;
}
