.album {
  height: 8rem;
}

.album:hover {
  cursor: pointer;
  transform: scale(0.9);
  transition: transform 0.2s ease-in;
}

.show_cons_1 h2 {
  font-size: 24px;
  color: #fff;
  margin-bottom: 2rem;
}

.list-movie {
  margin: 0 auto;
  width: 93%;
}

.box-movie {
  margin-top: 18%;
}

.imageAlbum {
  display: flex;
  gap: 1rem;
  overflow-x: auto;
}

.errorTitle p {
  font-size: 22px;
  color: #fa5252;
  text-align: center;
}

.post-paths {
  height: 12rem;
}

.post-paths:hover {
  cursor: pointer;
  transform: scale(0.9);
  transition: transform 0.2s ease-in;
}
